import { calendari18n } from "../../libraries/calendari18n";
import { FormatNumber } from "../../libraries/formatNumber";

class GenerateChartData 
{
    /**
     * @param {Array} data data from database
     * 
     * example use:
     * let generator = new GenerateChartData(data);
     * 
     * generator->add("totals->orders->sales", "valor");
     * generator->add("totals->orders->quantity", "ventas");
     * generator->add("totals->total_forms", "formularios");
     * 
     * let chart_data = generator->render();
     * 
     */
    constructor(data, options)
    {        
        this.options = options || {};
        if(!Array.isArray(Object.values(data))){
            console.error("argument #1 GenerateChartData constructor is not an array.")
            return false;
        }
        this.data = data;
        this.intervals = "days";
        this.labels = [];
        this.series = [];

        this.#set_limits();
        this.#set_labels();
    }
    #set_limits()
    {
        let dates = {};
        const app = this;
        let limit_on_months = this.options?.limit_on_months ?? false;
        let intervals = this.options?.intervals ?? "days";
        if(limit_on_months === false){return;}
        Object.values(this.data).map((value, key)=>{
            const date = Object.keys(this.data)[key];
            const splitted = date.split("-");
            const year = splitted[0]; 
            const month = splitted[1]; 
            const day = splitted[2]; 
            const field = `${year}-${month}`;
            
            if(typeof dates[field] === "undefined"){
                dates[field] = [];
            }
            dates[field].push(value)
        });
        let limiter = null;
        if(typeof limit_on_months === "function"){
            limiter = limit_on_months(dates);
        }

        if(limiter !== null){
            this.data = limiter
            this.intervals = intervals;
        }
    }
    #set_labels()
    {
        let app = this;
        this.labels = [];
        let length =  Object.keys(app.data).length;

        Object.keys(app.data).map(( date, key )=>{
            date = date.split("-");
            function get(data, el){
                return (+(date[el]) - 1);
            }
            let structure = "";
            if(this.intervals === "days"){
                structure = `${calendari18n.monthsShort[ get(date, 1) ]} ${date[2]}`;
            }

            if(this.intervals === "month"){
                structure = `${calendari18n.monthsShort[ get(date, 1) ]}, ${date[0]}`;
            }
            

            app.labels.push(structure);
        })
    }
    #array_walk(setter, item)
    {
        setter = setter.split("->");
        setter.map(( field )=>{
            item = item[field];                
        })
        return item;
    }
    #set_series(setter, metaname)
    {
        let app = this;

        
        let series_container = [];
        Object.values(app.data).map(( item, key )=>{
            let label = Object.keys(app.data)[key];
            let render_field = item;
            let data_saved = [];
            setter.map(( field )=>{
                render_field = render_field[field];                
            })
            if(typeof render_field !== "undefined"){
                data_saved.push(render_field)
            }

            
            data_saved.map(( value )=>{
                series_container.push({
                    meta: `
                        <span class="meta-date-chart font-size-12 fw400" >${label}</span>
                        <br>
                        <span class="meta-value-chart font-size-12 has-text-success" >$${FormatNumber(value, false)}</span>
                    `,
                    value: value,
                })
            })

        })

        this.series.push(series_container);
    }
    /**
     * @param {String} setter How to access data, for example: "totals->total_orders"
     * @param {String} metaname name of the meta name for values.
     */
     add(setter, metaname)
     {
        setter = setter.split("->");
        metaname = metaname || "Valor";        
        this.#set_series(setter, metaname);
     }
     render()
     {
        return {
            labels: this.labels,
            series: this.series,
        }
     }
}



class ChartistLimiter
{
    constructor(props)
    {
        props = props || {};
    }
    limit_on_months(months, quantity)
    {
        if(Object.values(months).length < quantity){
            return null;
        }
    
        let labels = {};
        Object.values(months).map(( days, key )=>{
            let date = Object.keys(months)[key];
    
            if(typeof labels[date] === "undefined"){
                labels[date] = {
                    totals: {
                        orders: {
                            counter: 0,
                            sales: 0,
                        }
                    }
                }
            }
            
    
            days.map(( day )=>{
                let currentCounter = labels[date]["totals"]["orders"]["counter"];
                let currentSales = labels[date]["totals"]["orders"]["sales"];
                let counter = day["totals"]["orders"]["counter"];
                let sales = day["totals"]["orders"]["sales"];
                labels[date]["totals"]["orders"]["counter"] = Math.round(currentCounter + counter);
                labels[date]["totals"]["orders"]["sales"] = Math.round(sales + currentSales);
            })
    
            
        })

        return labels;
    }
}



export {GenerateChartData, ChartistLimiter}