<script>
import { InlineRoute } from './../../../inline.routes.js';
import { RouterStore } from './../../../RouterStore.js';
import LoaderElement from './../../../components/Elements/LoaderElement.svelte';
import { ApplicationData } from './../../../data.application.route.js';
import { page } from '../../../turpial.route.store.js';
import { FormatNumber } from '../../../libraries/formatNumber.js';
import { ChecksSelectedData } from '../../../data.checks_selected.js';

let order = {};
let subtotal = 0;
let quantity_total = 0;
let countries = [];

function order_selected(process){
    process = process || "set";
    if(process == "set"){
        window.sm_order_selected = order.order_id;
    }else{
        delete window.sm_order_selected;
    }
}


fetch(`${$ApplicationData.api_host}public/api/v1/services/builder/orders/${$RouterStore.parameters.order}`, {
    headers: {'X-Token-Website': $ApplicationData.token_website}
})
.then((r)=>{
    return r.json();
}).then((r)=>{
    order = r;
    get_totals();
    order_selected('set');
})


function get_totals(){
    try {
        order.items.map(( item )=>{
            subtotal = subtotal + item.total

            quantity_total = quantity_total + item.quantity;
        })
    } catch (error) {
        page.route('projects/orders');
        return;
    }
    get_countries();
}

function get_countries(){
    countries = $ApplicationData.countries?.filter(( item ) => {
        return item.c2 === order.shipping_address["country"];
    });
}



let processed_by;

$: {
    if(order){
        processed_by = $ApplicationData.app_store.find(( app )=>{
            if(order.processed_by == app.brand_id){
                return app;
            }
        })
    }
}

function processHanddler() {
    ChecksSelectedData.update("from_path", $InlineRoute.path)
    page.route(`/projects/orders/${order.order_id}/process`);
}


</script>
{#if typeof order.order_id !== "undefined"}
    <div class="is-line flex-center-flex-start mb-2" style="position: relative;">
        <a href="#path:/projects/orders" on:click="{()=>{ order_selected("unset") }}" class="flex-center-flex-start">
            <i class="icon-tripod-arrow-back is-size-2"></i>
            <p class="is-hidden-touch" style="margin-left: 1rem;">volver</p>
        </a>
        <div class="flex-center-flex-end pr-alt-6" style="gap: 2rem;position:absolute;right:0;">
            <a href="#projects/" on:click|preventDefault="{()=>{ $ApplicationData.go_to_order(window.sm_order_selected) }}">Editar orden</a>
            {#if order.status === "pending"}
                <button type="button" class="button brand is-primary is-rounded" on:click={() => processHanddler()}>
                    Procesar orden
                </button>
            {/if}
        </div>
    </div>
    <section class="container-card has-text-gray w-100">
        <article class="card-item bg-gray px-5 py-4">
            <div class="flex-center-space-between is-size-5">
                <span class="fw700">Estado</span>
                <div class="flex-center" style="gap: 6px;">
                        {#if order.status === "pending"}
                            <i class="icon-tripod-package" style="color: var(--color-primary);"></i>
                        {:else if  order.status === "canceled"}
                            <i class="icon-tripod-warning" style="color: var(--color-danger);"></i>
                        {:else if order.status === "completed"}
                            <i class="icon-tripod-shipped" style="color: var(--color-success);"></i>
                        {:else if order.status === "shipped"}
                            <i class="icon-tripod-truck-moving" style="color: var(--color-primary);"></i>
                        {/if}
                    <span class="is-size-6 fw500">
                        {#if order.status === "pending"}
                             <span style="color: var(--color-primary);">En preparación</span>
                        {:else if  order.status === "canceled"}
                             <span style="color: var(--color-danger);">Cancelado</span>
                        {:else if order.status === "completed"}
                            <span style="color: var(--color-success);">Entregado</span>
                        {:else if order.status === "shipped"}
                            <span style="color: var(--color-primary);">En camino</span>
                        {/if}
                    </span>
                </div>
            </div>
            <hr>
            <div>
                <p class="fw700 is-size-5">Detalles</p>
                <div class="flex-center-space-between mt-4 is-flex-start">
                    <span class="fw600 mr-5">Nombre</span>
                    <span class="name-info has-spacing">{order.customer_name}</span>
                </div>
                <div class="flex-center-space-between mt-4 is-flex-start">
                    <span class="fw600 mr-5">Correo</span>
                    <span class="has-spacing">{order.customer_email}</span>
                </div>
                <div class="flex-center-space-between mt-4 is-flex-start">
                    <span class="fw600 mr-5">Teléfono</span>
                    <span>{order.shipping_address["phone"]}</span>
                </div>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600 mr-5">Metodo de pago</span>
                    <span>{order.payment_method}</span>
                </div>
            </div>
            <hr>
            <div>
                <p class="fw700 is-size-5">Datos de envío</p>
                <div class="flex-center-space-between mt-4">
                    <span class="fw600 mr-5">País / región</span>
                    <span class="has-text-right">{countries[0].name}</span>
                </div>
                <div class="flex-center-space-between mt-4 is-flex-start">
                    <span class="fw600 mr-5">Provincia</span>
                    <span class="has-spacing">{order.billing_address["address"]}</span>
                </div>
                <div class="flex-center-space-between mt-4 is-flex-start">
                    <span class="fw600 mr-5">Ciudad</span>
                    <span class="has-spacing">{order.billing_address["city"]}</span>
                </div>
                <div class="flex-center-space-between mt-4 is-flex-start">
                    <span class="fw600 mr-5">Adicional</span>
                    <span class="has-spacing">{order.billing_address["address2"]}</span>
                </div>
            </div>
        </article>
        <article class="card-item px-5 py-4">            
            <div>
                <div>
                    <span class="fw700 is-size-5 mr-5">Productos</span>
                    <span>
                        (Total
                            <span class="fw600">{order.items.length}</span>)
                    </span>
                </div>    
            </div>
            <div class="bg-gray container-product px-5 py-4 flex-center-space-between" style="flex-direction: column;">
                {#each order.items as products }
                    <div class="is-gap flex-center-space-between w-100">
                        <div class="img-container has-shadow">
                            <img src="{products.images}" alt="product-item">
                        </div>
                        <div class="w-80">
                            <div class="product-info">
                                <span class="fw600">{products.name}</span>
                                <span>
                                    x{products.quantity === 1 
                                    ? `${products.quantity} unidad` 
                                    : `${products.quantity} unidades`}
                                </span>
                            </div>
                            <div class="flex-center-space-between mt-3">
                                <div class="flex-center" style="gap: 8px;">
                                    <i class="icon-tripod-package" style="color: #415B76"></i>
                                    <span class="fw500">Producto físico</span>
                                </div>
                                <span class="fw500">$ {FormatNumber(products.total, false)}</span>
                            </div>
                        </div>
                    </div>        
                {/each}
                
            </div>
            <div class="px-5 py-4 fw500">
                <div class="paid-info">
                    <span class="fw500">Pagado</span>
                    <span class="fw500">
                        {order.paid === false ? 'No pagado' : 'Pagado'}
                    </span>
                </div>
                <div class="flex-center-space-between mt-5">
                    <div class="units-info">
                        <span>Subtotal</span>
                        <span>x{quantity_total} unidades</span>
                    </div>
                    <span>$ {FormatNumber(subtotal, false)}</span>
                </div>
                <div class="flex-center-space-between mt-3">
                    <span>Gastos de envío</span>
                    <span>$ {FormatNumber(order.shipping_amount, false)}</span>
                </div>
            </div>
            <hr>
            <div class="px-5 flex-center-space-between">
                <span class="fw500">Total</span>
                <span class="fw600">${FormatNumber(order.total, false)}</span>
            </div>
            {#if typeof processed_by !== "undefined"}
                <div class="bg-gray request-processing px-5 py-4 mt-5">
                    <div class="flex-center-space-between w-100">
                        <span class="fw700">Tramitación de pedido</span>
                        <div class="tag-float">
                            <span class="is-float" style="left: -111px;">
                                <p class="is-size-7 has-text-gray fw500">
                                    Esta orden 
                                    {#if order?.status === "completed"}
                                            fue procesada por
                                        {:else}
                                            está siendo procesada por
                                    {/if}
                                    {processed_by.name}
                                </p>
                            </span>
                            <img src="{processed_by.logos[1]}" alt="{processed_by.brand_id}-icon" class="icon-processed">
                        </div>
                    </div>
                    <div class="flex-center-space-between w-100 mt-4">
                        <span class="fw600">Número de guía</span>
                        <span class="fw600">{order.tracking_guide}</span>
                    </div>
                    {#if (order.tracking_url ?? "") !== ""}
                    <div class="flex-center-space-between w-100 mt-4">
                        <span class="fw600">URL de seguimiento</span>
                        <span class="">
                            <a href="{order.tracking_url}">
                                Ver estado de la orden
                            </a>
                        </span>
                    </div>
                    {/if}
                </div>
            {/if}
        </article>
    </section> 
    {:else}
    <LoaderElement style_container="min-height:400px;" />
{/if}


<style>

    .bg-gray{
        background-color: #F5F8FA;
    }

    .has-text-gray{
        color: #415B76;
    }

    .container-card{
        display: flex;
        flex-direction: row;
        gap: 30px;
    }

    .container-card .card-item{
        border-radius: 20px;
    }

    .container-card .card-item:nth-child(1){
        width: 40%;
    }
    .container-card .card-item:nth-child(2){
        width: 60%;
    }

    .container-card hr{
        background-color: #B4C0C9;
        height: 1px;
        margin-top: 1.5rem;
    }

    .w-100{
        width: 100%;
    }

    .w-80{
        width: 80%;
    }

    .product-info, .units-info, .paid-info{
        display: flex;
        align-items: center;
        gap: 26px;
    }

    .paid-info span:nth-child(2){
        background-color: rgba(180,192,201,0.25);
        padding: 2px 13px;
        border-radius: 18px;
    }

    .name-info{
        text-transform: lowercase;
    }

    .img-container{
        width: 77px;
        height: 77px;
    }

    .img-container img {
        width: 100%;
        height: 100%;
    }

    .icon-processed{
        width: 50px;
    }

    .is-line{
        width: 100%;
    }
    .is-line a{
        text-decoration: none;
        color: #415B76;
    }

    .is-gap{
        gap: 1rem;
    }

    .container-product{
        border-radius: 20px;
        gap: 1rem;
    }

    .request-processing{
        border-radius: 20px;
    }

    .has-spacing{
        text-align: right;  
        word-break: break-word;     
    }

    .is-flex-start{
        align-items: flex-start;
        flex-direction: row;
    }

    .tag-float .is-float{
        bottom: 61px;
    }

    @media only screen and (max-width: 1024px){
        .container-card{
            display: flex;
            flex-direction: column;
            gap: 30px;
        }
        .container-card .card-item:nth-child(1){
            width: 100%;
        }
        .container-card .card-item:nth-child(2){
            width: 100%;
        }
    }

    @media only screen and (max-width: 600px){
        .card-item{
            padding: 0.55rem !important;
        }
    }
    
    @media only screen and (max-width: 420px){
        .container-product{
            padding: 0.55rem !important;
        }
        .img-container{
            height: 90px;
            width: 90px;
        }
        .product-info{
            flex-direction: column;
            gap: 0;
        }
        .is-gap{
            gap: 0;
        }
        .product-info ~ div{
            flex-direction: column;
        }
    }
    
</style>