<script>
import AppForms from './../../../../libraries/coreApp/AppForms.js';
import { page } from './../../../../turpial.route.store.js';
import { ApplicationData } from './../../../../data.application.route.js';
import { turpial } from '../../../../libraries/turpial';
import { RouterStore } from '../../../../RouterStore';
import MediaQuery from './../../../../components/MediaQuery.svelte';
import { ButtonStatus } from '../../../../libraries/coreApp.js';
import { ApplicationInstaller } from '../effi/ApplicationInstaller.js';
export let type = "default";
export let saveButton = true;
export let steps;
export let logoUrl;
export let topInstructions = "none";
export let bottomInstructions = "none";
export let underLineText = "none";
export let FormDataApplication;
export let application_name = "";

let buttonSubmit = null;

let id_elements = "rd-"+turpial.random_string(6);

function switchInstaller(element) {
    FormDataApplication.update(( data )=>{
        data.form.install = element.checked;
        return data;
    })
}
function switchWebhooks(element) {
    FormDataApplication.update(( data )=>{
        data.form.send_orders = element.checked;
        return data;
    })
}


function activateButton(){
    buttonSubmit.removeAttribute("disabled");
}

function catchData(event){
    

    let form = event.target;
    form = AppForms.retrieve({
        form: form,
        targets: ".formElements"
    })

    
    ButtonStatus(buttonSubmit, "disabled");
    new ApplicationInstaller({
        ApplicationData: $ApplicationData,
        application_name: application_name,
        install: form.install,
        send_orders: form.send_orders,
        data: form,
        updater: ApplicationData,
        callback: function(data){
            ApplicationData.update("token_website", data.token);
            ApplicationData.update("app_store", data.builder_apps.data);
            ApplicationData.update("website_data", data.website);

            ButtonStatus(buttonSubmit, "enabled");
            buttonSubmit.setAttribute("disabled", "");
        }
    })
}

let container = null;
FormDataApplication.on_update((data)=>{
    let buttons = [...container.querySelectorAll(".save-button")];
    buttons.map(( button )=>{
        button.removeAttribute("disabled");
    });
})


let application = null;
$ApplicationData.app_store.map(( app )=>{
    if(app.brand_id === application_name){
        application = app;
    }
})

if(application === null){
    page.route("projects/applications");
}
</script>
{#if application !== null}
    <form action="" on:submit|preventDefault="{catchData}" on:change="{()=>{activateButton()}}">
        <section class="first-container full-size flex-column" bind:this="{container}">
            <div class="top-container full-size flex-center-space-between">
                <div class="logo-container">
                    <img src={logoUrl} alt="Logo">
                </div>
                <div class="actions-container flex-center-space-between">
                    {#if underLineText != "none"}
                        <a class="underline-text" href="#path:/projects/applications/{$RouterStore.parameters.application}/products">{underLineText}</a>
                    {/if}
                    {#if application.has_receiver_orders}
                        <div class="switch-container flex-center">
                            <p style="margin-right: 1rem;">Enviar ordenes automáticamente</p>
                            <div class="field" style="margin-bottom: 0;">
                                <input id={id_elements+"-preferred"} type="checkbox" data-boolean="true" name="send_orders"  class="switch formElements is-rounded" checked={$ApplicationData.website_data.installed_apps[application.brand_id]?.send_orders}>
                                <label for={id_elements+"-preferred"}></label>
                            </div>
                        </div>
                        <div class="division">
                            <!-- division  -->
                        </div>
                    {/if}
                    <div class="switch-container flex-center">
                        <p style="margin-right: 1rem;">{$ApplicationData.website_data.installed_apps[application.brand_id]?.installed === true ? "Activo" : "No instalada"}</p>
                        <div class="field" style="margin-bottom: 0;">
                            <input id={id_elements+"-installer"} type="checkbox" data-boolean="true" name="install"  class="switch formElements is-rounded" checked={$ApplicationData.website_data.installed_apps[application.brand_id]?.installed} >
                            <label for={id_elements+"-installer"}></label>
                        </div>
                    </div>
                    <MediaQuery query="(min-width: 900px)">
                        {#if saveButton}
                            <button type="submit" bind:this="{buttonSubmit}" class="save-button button is-rounded" disabled>Guardar</button>
                        {/if}
                    </MediaQuery>
                </div>
            </div>
            <div class="bottom-container full-size {type}">
                {#if type == "only-top"}
                    <slot><!-- optional fallback --></slot>
                {:else}
                    <div class="slot-container">
                        <slot><!-- optional fallback --></slot>
                        <MediaQuery query="(max-width: 900px)">
                            {#if saveButton}
                                <button type="submit" bind:this="{buttonSubmit}" class="save-button button is-rounded" disabled>Guardar</button>
                            {/if}
                        </MediaQuery>
                    </div>
                    <div class="steps-container">
                        <div class="steps p-6">
                            <ul>
                                <li class="fw600" style="font-size: 20px; margin-bottom: 1rem;">
                                    <h2 class="fw600">
                                        Instrucciones
                                    </h2>
                                </li>
                                {#if topInstructions != "none"}
                                    <li style="padding-bottom: 1rem; word-break: break-word;">{topInstructions}</li>
                                {/if}
                                {#each steps as step, index}
                                    <li class="step-item">
                                        <div class="step-tittle fw600">
                                            <p style="white-space: nowrap;">Paso {index + 1}</p>
                                        </div>
                                        <div class="step-description">
                                            <p style="word-break: break-word;">{@html step}</p>
                                        </div>
                                    </li>
                                {/each}
                                {#if bottomInstructions != "none"}
                                    <li style="padding-top: 1rem; word-break: break-word;">{bottomInstructions}</li>
                                {/if}
                            </ul>
                            <slot name="footer"></slot>
                        </div>
                    </div>
                {/if}
            </div>
        </section>
    </form>
{/if}

<style> 
.division{
    border-left: 1px solid var(--color-primary);
    height: 16px;
    margin-right: 1rem;
}
.first-container{
    min-height: 300px;
    color: #415B76;
}
.top-container{
    height: 90px;
    border-bottom: solid #35465b44 1px;
    padding-bottom: 1rem;
}
.top-container .underline-text{
    position: relative;
    text-decoration: underline;
    margin-left: auto;
    margin-right: 2rem;
}
.save-button{
    background: #296FFF;
    color: white;
    border: none;
}
.top-container .logo-container{
    display: flex;
    height: 100%;
    padding: 0 0 1rem 0;
}
.top-container .logo-container img{
    height: 100%;
}
.bottom-container{
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
}
.bottom-container .slot-container{
    width: 65%;
    padding-right: 1rem;
    display: flex;
    flex-direction: column;
}
.bottom-container .steps-container{
    width: 35%;
    min-height: 400px;
}
.bottom-container .steps-container .steps{
    width: 100%;
    min-height: 200px;
    background: var(--color-grey-background);
    border-radius: 20px;
    padding: 1rem;
}
.bottom-container .steps-container ul .step-item{
    display: flex;
    flex-direction: row;
    width: 100%;
    font-size: 16px;
    padding: 0.55rem 0;
}
.bottom-container .steps-container ul .step-item .step-tittle{
    margin-right: 1rem;
}
@media only screen and (max-width: 900px){
    .bottom-container.default{
        flex-direction: column-reverse;
    }
    .bottom-container.default .steps-container{
        width: 100%;
    }
    .bottom-container .slot-container{
        margin-top: 1rem;
        width: 100%;
        padding-right: 0;
    }
    .top-container .underline-text{
        position: relative;
    }
    .save-button{
        margin-left: auto;
    }
}
@media only screen and (max-width: 750px){
    .top-container{
        flex-direction: column;
        height: 120px;
    }
    .top-container .logo-container{
        height: 70%;
        padding: 0 0 0.55rem 0;
        margin-left: 0;
        margin-right: auto;
    }
    .top-container .actions-container{
        margin-left: auto;
        margin-right: 0;
    }
}
</style>
