function DynamicHorizontalAxis(options){
    return (chart)=>{
        const defaultOptions = {}

        options = options || defaultOptions;
        const target = options.target||document;
        const intervals = options.intervals||"days";
        chart.on('created', function(data) {
            const nodes = [...target.querySelectorAll(".ct-label.ct-horizontal")];
            const last_child = nodes.at(-1);
            const first_child = nodes[0];
            const total = nodes.length;
            let limit = 6;
            const breaker = total / limit;


            function add(node){
                if(node === null || typeof node === "undefined"){return;}
                node.classList.add("show-label");
            }
            function remove(node){
                if(node === null || typeof node === "undefined"){return;}
                node.classList.remove("show-label");
            }


            for (let index = 0; index < nodes.length; index++) {
                if(index % 2 === 0){
                    const node = nodes[index];
                    add(node);
                }
            }
            remove(last_child);
            return;

            if(intervals === "month"){
                
            }

            for (let index = 1; index < limit; index++) {
                const point = Math.round(index * breaker);
                const node = nodes[point];
                add(node);
            }

            
            add(first_child);
            add(last_child);
        });
    }
}

export {DynamicHorizontalAxis}