/**
 * compressNumberString: function to get a shor format of number is pased like param
 * @param num: a number
 */

 const compressNumberString = (num) => {
    let stringNumber = num.toString();
    let finalFormat = "";
    let counter = 0;
    let shortFinalFormat = "";
    stringNumber = stringNumber.split("").reverse().join("");
    if (num >= 1000) {
        for (const iterator of stringNumber) {
            finalFormat += iterator;
            counter += 1;
            if (counter === 3) {
                finalFormat += ".";
                counter = 0;
            }
        }
        finalFormat = finalFormat.split("").reverse().join("");
        if (finalFormat.indexOf('.') === 0) {
            finalFormat = finalFormat.slice(1);
        }
        if (finalFormat.includes('.')) {
            let index = finalFormat.indexOf('.') + 2;
            for (const i of finalFormat) {
                shortFinalFormat += i;
                if (shortFinalFormat.length >= index) {
                    if (num >= 1000 && num < 1000000) {
                        return shortFinalFormat + "K";
                    }else if (num >= 1000000) {
                        return shortFinalFormat + "M";
                    }else{
                        return shortFinalFormat;
                    }
                    
                }
            }
        }
    }else{
        return num;
    }
}

export { compressNumberString }
