<script>
export let classes = "";
export let id = "";
export let style = "";
export let path = "";
export let to = null;
export let on_click = function(){};

import { page } from '../../turpial.route.store.js';
if(typeof to === "string" || typeof to === "number"){
	let current = $page.path;
	current = current.slice(-1) === "/" ? current+"" : current+"/";
	path = current+`${to}`
}
</script>
<a on:click={on_click} class="{classes}" style="{style}" id="{id}" href="{page.getPath(path)}" on:click|preventDefault={()=>{ page.route(path); }}>
	<slot></slot>
</a>