<script>
	import { ShadowRoot } from './../../ShadowRoot.js';
import { onMount } from 'svelte';
import { chartist } from "../../libraries/chartist";
import { turpial } from '../../libraries/turpial';
import {GenerateChartData, ChartistLimiter} from './GenerateChartData'
import {DynamicHorizontalAxis} from './Extensions/DynamicHorizontalAxis'
import { compressNumberString } from "../../libraries/compressNumberString";
export let type = "sales-value";
export let chartTittle = "Tittle";
export let data_charts;

let chartId = "chart-" + turpial.random_string(5);

/**
 * limiter for large data (some months)
 */
const limiter = new ChartistLimiter();


let generator = new GenerateChartData(data_charts.get(), {limit_on_months: (months)=> limiter.limit_on_months(months, 5), quantity_months: 5, intervals: "month"});

if (type === "sales-value") {
    generator.add("totals->orders->sales", "valor ($)");
}else if (type === "sales-counter") {
    generator.add("totals->orders->counter", "Ventas");
}


let rendered_data = generator.render();
let labels_availables = [];
$: {
    if(data_charts.get()){
        chartist(()=>{
            generator = new GenerateChartData(data_charts.get(), {limit_on_months: (months)=> limiter.limit_on_months(months, 5), quantity_months: 5, intervals: "month"});
            if (type === "sales-value") {
                generator.add("totals->orders->sales", "valor ($)");
            }else if (type === "sales-counter") {
                generator.add("totals->orders->counter", "Ventas");
            }
            rendered_data = generator.render();
            

            new RenderChart({
                intervals: generator.intervals,
            });
        })
    }
}

class RenderChart
{
    constructor({intervals})
    {
        this.intervals = intervals || "days";
        try {
            this.init();
        } catch (error) {
            console.error(error)
        }
    }
    init()
    {
        const app = this;
        let data = rendered_data;
        app.removed_indexes = [];
        let options = {
            fullWidth: true,
            fullHeight: true,
            low: 0,
            showArea: true,
            showGrid: true,
            axisY: {
                scaleMinSpace: 30,
                type: chartist.FixedScaleAxis,
                labelInterpolationFnc: function(value) {
                    if (type === "sales-value") {
                        return "$" + compressNumberString(value);
                    }else{
                        return value;
                    }
                }
            },
            axisX: { 
                labelInterpolationFnc: function (value, index) {
                    return value;
                },
                scaleMinSpace: 30,
                offset: 60,
                type: chartist.FixedScaleAxis,
            },
            plugins: [
                Chartist.plugins.tooltip({
                    metaIsHTML: true,
                    currency: '$',
                    class: 'tooltip',
                }),
                DynamicHorizontalAxis({
                    target: $ShadowRoot,
                    intervals: app.intervals
                }),
            ],
        };

        

        /**
         * initiate chart.
         */
        try {
            let chart = new Chartist.Line($ShadowRoot.querySelector(`#${chartId}`), data, options);
        } catch (error) {
            
        }
    }
}

onMount(()=>{
    chartist(()=>{
        new RenderChart({
            intervals: generator.intervals,
        });
    })
})


</script>
<div class="chart-container flex-column has-shadow-light">
    <div class="chat-tittle flex-center-flex-start">
        <p class="fw600 py-alt-2">{chartTittle}</p>
    </div>
    <div style="width: 100%; height: fit-content !important; display:flex; align-items:center;" class="ct-chart ct-golden-section" id="{chartId}"></div>
</div>

<style>
.ct-chart:global( .tooltip .chartist-tooltip-value){
    display: none;
}
.chart-container{
    padding: 2rem;
    margin: auto;
    border-radius: 20px;
}
.chat-tittle{
    width: 100%;
}
.ct-chart:global( .tooltip){
    background-color: #F5F8FA;
    overflow: hidden;
    border-radius: 8px;
    color: #415B76;
}

.ct-chart:global( .tooltip::first-line){
    font-size: 12px !important;
    font-weight: 400;
}

/* Styles for line */
.ct-golden-section:global( .ct-series-a .ct-line ){
    stroke: var(--color-primary);
    stroke-width: 1px;
}

/* Styles for points */
.ct-golden-section:global( .ct-series-a .ct-point){
    stroke: var(--color-primary);
    stroke-width: 6px;
}

/* Styles for grid */
.ct-golden-section:global( .ct-grid){
    stroke-dasharray: 0;
}

/* Styles for vertical grid */
.ct-golden-section:global( .ct-grid.ct-horizontal){
    opacity: 0;
}
.ct-golden-section:global( .ct-grid.ct-horizontal):hover{
    opacity: 1;
    stroke-width: 2px;
    stroke: #cddaf5;
}

.ct-golden-section:global( .ct-grid.ct-vertical:nth-child(1)){
    display: 0;
    opacity: 0;
}

.ct-golden-section:global( .ct-grid.ct-vertical){
    stroke-width: 0.3px;
}

.ct-golden-section:global( .ct-labels .ct-vertical){
    display: flex;
    justify-content: flex-start;
}
.ct-chart :global(.ct-label.ct-horizontal:not(.show-label)){
    opacity: 0;
}
.ct-chart :global(.ct-area){
    fill: var(--color-primary)
}
.ct-golden-section:global( .ct-labels .ct-horizontal){
    font-size: 12px;
    color: #415B76;
    font-weight: 400;
}

@media only screen and (max-width: 600px){
    .chart-container{
        padding: 1rem;
        border-radius: 12px;
    }
}

</style>
