<script>
import { onMount } from 'svelte';

import { global_variables } from '../../global.variables';

export let listHeader = [];
export let paginatorData = {};
export let updateAllChecks = [];
export let fetcher = {};

let tableContainer;

let checkboxAll;

function setAllActives(e){
    let checkAtLimit = [];
    let checks = [...tableContainer.querySelectorAll('.input-checkbox:not(.checkAll)')];

    for (let item of checks) {
        if (checkAtLimit.length < paginatorData.data.checksLimit) {
            if (e.target.checked) {
                if (!item.hasAttribute('disabled')) {
                    item.checked = true;
                    checkAtLimit.push(item.id);
                }
            }  
        }
    }
    if (e.target.checked) {
        updateStateCheckboxes(checkAtLimit);
    }else{
        checkAtLimit = [];
        updateStateCheckboxes(checkAtLimit);
    }
}

function updateStateCheckboxes(check){
    if(paginatorData?.data?.listData?.data.length > 0){
        updateAllChecks(check);

        paginatorData = paginatorData;
    }
}

onMount(()=>{
    let ths = [...tableContainer.querySelectorAll(".th-elements .sorter")];

    ths.map(( node )=>{
        if(node.dataset.value === paginatorData.data.conditions.orderby){
            node.dataset.sortby = paginatorData.data.conditions.sortby;
            node.classList.add("active");
        }
    })
})

</script>
<style>
.w-100{
    width: 100%;
}
.table thead th{
    font-size: 1rem;
    text-align: left;
    color: #415B76;
}

thead, tbody{
    text-align: center;
}

:global(.table td){
    vertical-align: middle !important;
}
.sorter{
    height: fit-content;
}
.sorter:global([data-sortby='asc']){
    transform: rotate(180deg);
    transition: all 0.3s ease;
}
.sorter:global(.active){
    color: var(--color-primary);
}
.sorter{
    transform: rotate(0deg);
    transition: all 0.3s ease;
}
</style>
  
<table class="table is-borderless w-100 is-hoverable" bind:this={tableContainer}>
    <thead class="w-100">
        <tr>
            {#each listHeader.map(( item )=>{
                let element = {}
                if(typeof item === "string"){
                    element.value = item;
                }else{
                    element = item;
                }


                return element;
            }) as item }
                <th class="fw800 th-elements">
                    {#if item.value === "Checkbox"}
                         <input type="checkbox" class="input-checkbox checkAll" bind:this={checkboxAll} on:click={(e) => setAllActives(e)}>
                    {:else}
                         <p class="is-flex is-justify-content-space-between is-align-items-baseline">
                            <span>
                                {item.value}
                            </span>
                            {#if typeof item.sorter !== "undefined"}
                                <i data-value="{item.sorter}" class="icon-tripod-arrow-small-down sorter font-size-12 pointer" on:click="{function(e){
                                    let ths = [...tableContainer.querySelectorAll(".th-elements .sorter")];
                                    let target = e.target;
                                    ths.map(( node )=>{
                                        if(target !== node){
                                            node.removeAttribute("data-sortby");
                                        }
                                    })

                                    let sort = target.dataset?.sortby;
                                    if(sort === "asc"){
                                        target.dataset.sortby = "desc";
                                    }else{
                                        target.dataset.sortby = "asc";
                                    }

                                    global_variables.topbar.show();

                                    paginatorData.update(( object )=>{
                                        object.conditions.orderby = item.sorter;
                                        object.conditions.sortby = target.dataset.sortby;
                                        return object;
                                    })
                                    fetcher.fetch(()=>{
                                        global_variables.topbar.hide();
                                    }) 
                                }}"></i>
                            {/if}
                         </p>                   
                    {/if}
                </th>
            {/each}
        </tr>
    </thead>
    <tbody class="has-border">
        <slot>

        </slot>
    </tbody>
</table>