import { ApplicationData } from "../../data.application.route";

class ProjectWebsiteInstance
{
    callback = function(){};
    constructor(ApplicationData)
    {
        this.ApplicationData = ApplicationData || {};
    }
    fetch(project_selected, website_selected)
    {
        let app = this;
        project_selected = +(project_selected ?? this.ApplicationData?.project_selected?.value);
        website_selected = +(website_selected ?? this.ApplicationData?.website_selected?.value);

        let id = btoa(`${project_selected}_${website_selected}`);
        var queryParams = new URLSearchParams(window.location.search);
        
        turpial.fetch({
            url: `${this.ApplicationData.api_host}public/api/v1/services/user/website_selected/${id}`,
            type: "text",
            method: "GET",
            ready: function(responses){  
                let response = JSON.parse(responses[0]);
                ApplicationData.update("token_website", response.token);
                ApplicationData.update("app_store", response.builder_apps.data);
                ApplicationData.update("website_data", response.website);


                ApplicationData.update("project_id", response.website.project_id);
                ApplicationData.update("website_id", response.website.website_id);


                queryParams.set("project_id", response.website.project_id);
                queryParams.set("website_id", response.website.website_id);
                
                // Replace current querystring with the new one.
                history.pushState(null, null, "?"+queryParams.toString()+window.location.hash);
                app.callback();
            }
        })
    }
}

export {ProjectWebsiteInstance}