<script>
import { ShadowRoot } from './../../../../ShadowRoot.js';
import LoaderElement from './../../../../components/Elements/LoaderElement.svelte';
import Table from "../../../../components/Table/Table.svelte";
import { AppDate } from "../../../../libraries/coreApp";
import { ApplicationData } from '../../../../data.application.route';
import { ChecksSelectedData } from '../../../../data.checks_selected.js';
import { FormatNumber } from "../../../../libraries/formatNumber";



export let paginatorData;
export let fetcher = {};

let checkList = [];
let countries = [];
let checkPermision = true;


$:{
    if(paginatorData?.data){
        checkList = $ChecksSelectedData.checks_selected;
        paginatorData?.data?.listData?.data.map((item) => {
            if($ChecksSelectedData.checks_selected.includes(item.order_id)){
                return item.isChecked = true;
            }
            else{
                return item.isChecked = false;
            }
        });
        paginatorData = paginatorData;
    }
}

function checkboxActive(id) {
    let element = $ShadowRoot.getElementById(`${id}`);

    if($ChecksSelectedData.checks_selected.length < paginatorData.data.checksLimit){
    
        checkList = $ChecksSelectedData.checks_selected;
    
        if(element.checked){
            checkList = [...checkList, element.id];
        }
        else{
            let checkExists = checkList.filter((check) => check !== element.id);
            checkList = checkExists;
        }
    
        paginatorData.update((object) => {
            object.checks_selected = checkList;
            return object;
        })
    
    
        ChecksSelectedData.update("checks_selected", checkList);
    }
    else{
        checkPermision = false;
        alert("No puedes seleccionar mas de 4");
      
        element.checked = false;
    }
}

function updateAllChecks(data) {
    checkList = data;

    paginatorData.update((object) => {
        object.checks_selected = checkList;
        return object;
    })


    ChecksSelectedData.update("checks_selected", checkList);
}
/*
function get_countries(){
    let countriesCode = paginatorData?.data?.listData?.data?.map(( item ) => {
        return item.shipping_address.country;
    });

    let countriesCodeFromApi = $ApplicationData.countries?.filter(( item ) => {
        return countriesCode.includes(item.c2);
    });

    countriesCode.map((item) => {
        countriesCodeFromApi.map((item2) => {
            if(item === item2.c2){
                countries.push(item2.name);
            }
        })
    })
}

get_countries();*/



</script>
<Table listHeader={[{value: 'Checkbox'}, {value: 'Orden', sorter: 'order_id'},{value: 'Fecha', sorter:'created_at'},{value: 'Destinatario', sorter:'customer_email'},{value: 'Total', sorter:'total'}, {value: 'Estado', sorter:'status'}, {value: 'Aplicaciones'}, {value: '-'}]} paginatorData={paginatorData} updateAllChecks={updateAllChecks} fetcher={fetcher} >
    {#each paginatorData?.data?.listData?.data as order, i}
        <tr class="w-100">
            <td class="list-item-table">
                <input type="checkbox" class="input-checkbox" id={order.order_id} disabled={order.status === "pending" ? false : true} checked={order?.isChecked ? true : false}  on:click={() => checkboxActive(order.order_id)}>
            </td>
            <td class="list-item-table">{order.order_id}</td>
            <td class="list-item-table">{AppDate.onlydate(order.created_at, "date")}</td>
            <td class="list-item-table">
                <p style="overflow-wrap: anywhere;">
                    {order.customer_name}<br>
                    {order.customer_email}
                </p>
            </td>
            <td class="list-item-table">${FormatNumber(order.total, false)}</td>
            <td class="list-item-table" style="color: {order.statusHanddler?.color};"><div class="flex-center-flex-start"><i class="{order.statusHanddler?.icon}"></i> <p style="white-space: nowrap;">{order.statusHanddler?.text}</p></div></td>
            <td class="list-item-table">
                <div class="is-flex is-align-items-center gap-17">
                    {#if Object.values(order.sent_to).length === 0}
                         <p class="has-text-centered">
                            -
                         </p>
                    {/if}
                    {#each Object.values(order.sent_to).map(( times, key )=>{

                        // create logo images...


                        let name = Object.keys(order.sent_to)[key];
                        


                        let application = $ApplicationData.app_store.find(( app )=>{
                            if(app.brand_id === name){
                                return app;
                            }
                        })
    
                        if((application?.brand_id ?? null) !== null){
                            application.times = times;
                            return application;
                        }
                    }) as app}
                        {#if typeof app !== "undefined"}
                            <figure title="esta orden has sido enviada {app.times} {app.times > 1 ? "veces" : "vez"} a {app.brand_id}">
                                <img class="logos-apps" src="{app.logos[0]}" alt="{app.brand_id}">
                            </figure>
                        {/if}
                    {/each}
                </div>
            </td>
            <td class="list-item-table">
                <a href="#path:/projects/orders/{order.order_id}">
                    Ver más
                </a>
            </td>
        </tr>   
    {/each}
</Table>
<style>
.logos-apps{
    max-width: 32px;
}
.w-100{
    width: 100%;
}
.list-item-table{
    font-size:0.90rem;
    text-align: left;
    color: var(--color-grey);
    font-weight: 500;
} 
.link-item{
    font-size: 1rem;
    font-weight: 500;
    color: #296FFF;
}

</style>