<script>
import { ApplicationData } from './../../data.application.route.js';
import DateDropdowns from '../../components/Dropdowns/DateDropdowns.svelte';
import Calendar3Pod from './../../Calendar3Pod.svelte';
import { onMount } from 'svelte';
export let dashboardData = {};
export let dashboard_fetch_all = {};
let openOutside;
let personalizer = null;
let personalizerDates = [];

let dateValue;

let getOpen = (app)=> {
    openOutside = app;
}

let getData = (data)=>{
    if(data){
        dateValue = data;
    }
    if (personalizer) {
        personalizerDates = dateValue.split(" - ");
        dashboardData.update((object)=>{
            object.from = Number.parseInt(new Date(personalizerDates[0]).getTime()  / 1000);
            object.until = Number.parseInt(new Date(personalizerDates[1]).getTime()  / 1000);
            return object; 
        })
        dashboard_fetch_all();
    }
    personalizer = false;
}

function openDatePicker() {
    personalizer = true;
    openOutside.step = 1;
    openOutside.open(openOutside.fromInput);
}

function on_change(name, value, event){
    if (value === "personalizado") {
        openDatePicker();
    }else if (value === "todo") {
        dashboardData.setJson("from", "");
        dashboardData.setJson("until", "");
        dateValue = null
        dashboard_fetch_all();
    }else{
        let from_date = Number.parseInt((new Date(new Date().setDate(new Date().getDate() - value)).getTime()) / 1000);
        let until_date = Number.parseInt(new Date().getTime()  / 1000);

        dashboardData.update((object)=>{
            object.from = from_date;
            object.until = until_date;
            return object; 
        })
        dashboard_fetch_all();
    }
}

onMount(()=>{
    new Calendar3Pod({
        target: document.body,
        props: {
            getOpen: getOpen,
            getData: getData,
            props: {
                visivility: "hidden",
                type: "date",
                twelveHour: "true",
                name: "dates",
                "class": "formsElements",
                "isRange": "true",
            }
        }
    })
})

</script>
<style>
.has-text-gray{
    color: #415B76;
}

.date-value{
    position: absolute;
    right: 0;
    top: 48px;
    cursor: pointer;
    z-index: 5;
}

</style>
<section class="pb-6">
    <div class="is-relative is-flex is-justify-content-space-between">
        <h2 class="fw600 font-size-26">
            {$ApplicationData.website_title}
        </h2>
        <DateDropdowns on_change={on_change}/> 
        {#if dateValue}
            <span class="date-value has-text-gray is-size-7" on:click={()=>{openDatePicker()}}>{dateValue}</span>
        {/if}
    </div>
</section>
    