<script>
	import LoaderElement from './../components/Elements/LoaderElement.svelte';
import { ApplicationData } from './../data.application.route.js';
import { ProjectWebsiteInstance } from './LayoutLibraries/ProjectWebsiteInstance.js';

let show = false;

var queryParams = new URLSearchParams(window.location.search);
let project_selected = +(queryParams.get('project_id') ?? 0);
let website_selected = +(queryParams.get('website_id') ?? 0);
let instanceProject = new ProjectWebsiteInstance($ApplicationData);

// scroll_top
window.scroll_top();

instanceProject.callback = function(){    
    fetch(`${$ApplicationData.api_host}public/api/v1/services/user/projects/${$ApplicationData.user.public_key}`)
    .then((r)=>{
        return r.json();
    })
    .then((r)=>{
        ApplicationData.update("projects", r.projects ?? []);
        ApplicationData.update("has_projects", true);
        
        

        let project_list = $ApplicationData.projects
        let project = null;
        project_list.map(( project )=>{
            if((+project.id) === (+$ApplicationData.project_id)){
                ApplicationData.update("project_selected", {
                    name: project.name,
                    value: project.id,
                })

                project.websites.map(( website )=>{
                    if((+website.id) === (+$ApplicationData.website_id)){
                        ApplicationData.update("website_selected", {
                            name: website.name,
                            value: website.id,
                            subdomain: website.subdomain,
                        })

                        show = true;
                    }
                })
            }
        })


        
    })
}

if($ApplicationData.website_selected == null){
    
    instanceProject.fetch(project_selected, website_selected);
}else{
    show = true;
}





    
</script>
<section class="general-container flex-column-space-evenly">
    <main class="columns is-multiline is-centered">
        <div class="column is-9">
            <div class="general-card-container has-shadow">
                {#if show}
                    <slot><!-- optional fallback --></slot>
                    {:else}
                        <LoaderElement style_container="min-height:400px;" />
                {/if}
            </div>
        </div>
    </main>
</section>


<style>
    .general-container{
        min-height: 100vh;
        background: #F5F6FA;
        color: #415B76;
        padding: 3.25rem 1.25rem;
    }

    .general-card-container{
        background: white;
        border-radius: 20px;
        min-height: 300px;
        height: fit-content;
        padding: 36px 4.25rem;
    }

    @media only screen and (max-width: 1023px){
        .general-container{
            width: 100%;
            margin-left: 0;
        }
    }

    @media only screen and (max-width: 600px){
        .general-container{
            padding: 6.5rem 0.25rem;
        }
        .general-card-container{
            width: 100% !important;
            padding: 36px 1rem;
        }
    }
    .general-container:global(.home-styles .general-card-container){
        background-color: #fff !important;
        background: url('https://raw.githubusercontent.com/3pod-us/3pod-us.github.io/master/pub-img/900.jpg'), #fff !important;
        background-repeat: no-repeat !important;
        min-height: 580px !important;
        background-size: contain !important;
    }
    @media only screen and (max-width:1023px){
        .general-container:global(.home-styles .general-card-container){
            background-color: #fff !important;
            background: #fff !important;
        }
    }
</style>