const chartist = function(callback){
    if(typeof Chartist !== "undefined"){
        callback();
        return;
    }
    turpial.fetch({
        url: [
            "https://cdn.jsdelivr.net/gh/gionkunz/chartist-js@latest/dist/chartist.min.js",
            "https://cdn.jsdelivr.net/gh/tmmdata/chartist-plugin-tooltip@latest/dist/chartist-plugin-tooltip.min.js",
        ],
        ready: ()=>{
            callback();
        },
    })
}



export { chartist }