<script>
    import { reporter, ValidationMessage } from '@felte/reporter-svelte';
    import { validator } from '@felte/validator-yup';
    import * as yup from 'yup';
    import { createForm } from 'felte';

    // export let changeStep;
    export let dataForms;
    export let changeStep;

    yup.setLocale({
        mixed: {
            default: "No válido",
            required: "El campo no puede estar vacío"
        },
        string: {
            default: "El campo no puede estar vacío",
            email: "Ingrese un email válido",
            min: "El campo no puede estar vacío"
        },
    });

    const schema = yup.object({
        user: yup.string().required(),
        password: yup.string().required(),
    });

    const { form, errors } = createForm({
        initialValues: {
            user: dataForms.user,
            password: dataForms.password,
        },
        extend: [
            validator({ schema }),
            reporter()
        ],
        validateSchema: schema,
        onSubmit: async (values) => {
            changeStep(2);
            dataForms = values;
        },
    })

</script>

<form use:form class="mt-5">
    <div class="field">
        <label class="label" for="user">Usuario</label>
        <div class="control">
            <input class="input formsElements w-100" type="text" id="user" placeholder="Usuario" name="user" bind:value="{dataForms.user}">
        </div>
        <ValidationMessage level="errors" for="user" let:messages={messages}>
            <p class="help is-danger">{$errors.user || ''}</p>
        </ValidationMessage>
    </div>
    <div class="field">
        <label class="label" for="password">Contraseña</label>
        <div class="control">
            <input class="input formsElements w-100" type="password" id="password" placeholder="Contraseña" name="password" bind:value="{dataForms.password}">
        </div>
        <ValidationMessage level="errors" for="password" let:messages={messages}>
            <p class="help is-danger">{$errors.password || ''}</p>
        </ValidationMessage>
    </div>
    <button type="submit" class="effi__btn mt-2" >Continuar</button>
</form>

<style>
    .w-100
    {
    width: 100%;
    }
    .effi__btn{
        background: #EF8503 padding-box;
        font-size: 14px;
        padding: 8px;
        width: 100%;
        border-radius: 8px;
        color: #fff;
    }
</style>
