import { turpial } from "./turpial";

class ExcelFiles 
{
    constructor(callback){
        this.header = {};
        this.files = [];
        let app = this;
        
        turpial.fetch({
            url: [
                "https://cdn.jsdelivr.net/npm/json2csv", 
                "https://cdn.jsdelivr.net/gh/Yerikmiller/3pod-us.github.io@master/zipfile/jszip.min.js",
                "https://cdn.jsdelivr.net/gh/Stuk/jszip@master/dist/jszip.min.js",
            ],
            ready: ()=>{
                callback();
            }
        })
    }
    add(name, data)
    {
        try {
            const parser = new json2csv.Parser(data);
            const csv = parser.parse(data);

            this.files.push({
                name: name+".csv",
                csv: csv,
            })
        } catch (err) {
            console.error(err);
        }
    }
}

export {ExcelFiles}