<script>
import { page } from './turpial.route.store.js';
import { ApplicationData } from './data.application.route.js';
import { InlineRoute } from './inline.routes.js';
import Styles from './Styles.svelte';
import Router from './components/Router.svelte';
import {turpial} from './libraries/turpial';
import { onMount } from 'svelte';
import { ShadowRoot } from './ShadowRoot.js';



InlineRoute.activate();




/**
 * props from outside
 */
let props = $$props;

let is_online = props.is_online ?? false;
let host = props.host ?? null;
let has_projects = props.has_projects ?? false;
let go_to_project = props.go_to_project ?? function(){ page.route("/projects") };
let go_to_order = props.go_to_order ?? function(){  };
let title = props.title ?? "Dashboard";
let user_testing = {};

if(is_online === false){
    user_testing = {
        //form test itZiR6HFniTJGNcVCfFdTMnwbA8IVS3l
        //order test 5OITonYkGPaRtFkkemjhbpV3bKHRLj_H
        public_key: "itZiR6HFniTJGNcVCfFdTMnwbA8IVS3l"
    }
}

let user = props.user ?? user_testing;




// is_online = "localhost";


ApplicationData.update("is_online", is_online);
ApplicationData.update("user", user);
ApplicationData.update("host", host);
ApplicationData.update("has_projects", has_projects);
ApplicationData.update("go_to_project", go_to_project);
ApplicationData.update("go_to_order", go_to_order);



if($ApplicationData.is_online === true){
    ApplicationData.update("api_host", "https://app.3pod.io/");
}else if($ApplicationData.is_online === false){
    ApplicationData.update("api_host", "https://dev.3pod.io/");
}else if($ApplicationData.is_online === 'localhost'){
    ApplicationData.update("api_host", "http://localhost/app.3pod.io/");
}

if(($InlineRoute.path === "" || $InlineRoute.path === "/") && has_projects === true){
    page.route("projects/")
}

/* console.table({
    host: $ApplicationData.api_host
}); */


fetch(`https://cdn.jsdelivr.net/gh/Yerikmiller/3pod-us.github.io@countries-c3/countries.json`)
.then((r)=>{
    return r.json();
}).then((r)=>{
    ApplicationData.update("countries", r);    
})

window.scroll_top = function(){
    window.scrollTo({ top: 0, behavior: 'smooth' });
}

let show = false;
let injected_styles = null;
onMount(()=>{    
    let root_styles = document.querySelector("#injected-css-sv");
    injected_styles.appendChild(root_styles);
    show = true;
    ShadowRoot.update(document.querySelector(".svelte-websites-dashboard .svelte-app-child").shadowRoot)
    //ShadowRoot.update(document);




    turpial.linkCSS({
        target: [
            document.head,
            $ShadowRoot
        ],
        url: [
            "https://cdn.jsdelivr.net/gh/Yerikmiller/3pod-us.github.io@tpi-1.0.1/icons/platform/tripod-pack/tripod-pack.css",
            "https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;700;800&display=swap",
            "https://use.fontawesome.com/releases/v5.12.1/css/all.css",
        ]
    })

    turpial.linkCSS({
        target: $ShadowRoot,
        url: [
            "https://cdn.jsdelivr.net/chartist.js/latest/chartist.min.css", 
            "https://cdn.jsdelivr.net/gh/tmmdata/chartist-plugin-tooltip@latest/dist/chartist-plugin-tooltip.min.css"
        ],
    })

})

</script>
<svelte:head>
    <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no" />
    <meta name="robots" content="index, follow">
    <meta name="charset" content="UTF-8">
    <title>{title}</title>
    <script>
        turpial = turpial;
        __HOST = __HOST;
    </script>
    <style>
        body, html{
            background-color: #f5f8fa;
            overflow-x: hidden !important;
            overflow-y: overlay !important;
        }
    </style>
</svelte:head>
<!-- container to inject styles -->
<div class="injected-css-sv-container" bind:this="{injected_styles}"></div>
<Styles />
{#if show}
    <Router CustomRoute={$InlineRoute}/>
{/if}
