import { turpial } from '../libraries/turpial';
import App from '../MergedComponents.svelte'; // import App from '../App.svelte'; or import App from '../ComponentTest.svelte';
let app = document.querySelector(".svelte-websites-dashboard");
app = turpial.mount(app, `<div class="svelte-app-child index-module"></div>`);

app = app.attachShadow({mode: 'open'});

window.WebsitesDashboard.target = app;

app = new App(window.WebsitesDashboard);