<script>
import NoElementsTable from './../../../components/Elements/NoElementsTable.svelte';
import LoaderElement from './../../../components/Elements/LoaderElement.svelte';
import HeaderBoxProjects from './../HeaderBoxProjects.svelte';
import ListSalesReport from './components/ListSalesReport.svelte';
import Paginator from '../../../components/paginator/Paginator.svelte';
import SearchInput from '../../../components/Inputs/searchInput.svelte';
import { CustomStore } from "../../../libraries/customStore/CustomStore.js";
import { FetchApp } from "../../../libraries/fetchApp";
import StateDropdown from '../../../components/Dropdowns/StateDropdown.svelte';
import { ApplicationData } from '../../../data.application.route';
import { ChecksSelectedData } from '../../../data.checks_selected.js';
import { page } from '../../../turpial.route.store';

let salesPaginator = new CustomStore( 
    {
        currentPage: 1,
        checksLimit: 40,
        limit: 0,
        url: `${$ApplicationData.api_host}public/api/v1/services/builder/orders`,
        headers: {
            'X-Token-Website': $ApplicationData.token_website,
        },
        conditions: {
            search: "",
            perpage: 10,
            offset: 0,
            sortby: "desc",
            orderby: "created_at",
            fields: "status,customer_name,customer_email,total,order_id,created_at",
            from: "",
            until: "",
            status: "",
        },
        statusOrders: {
            pending: 0,
            shipped: 0,
            completed: 0,
            canceled: 0,
            processing: 0
        },
        checks_selected: []
    },
    function(){
        salesPaginator = salesPaginator;
    }
);

/* $: {
    if(salesPaginator.data?.checks_selected){
        console.log(salesPaginator.data?.checks_selected);
    }
} */

let orders_by_process = 0;
let orders_processed = 0;
let list = [];
let setAllActives;
let setElement;
const list_application = new FetchApp(salesPaginator, (data)=>{
    salesPaginator.setJson("listData", data);
    list = data;
    salesPaginator.setJson("limit", Math.ceil(data.items / salesPaginator.data.conditions.perpage));
})


list_application.before_fetch = ()=>{
    salesPaginator.update(function(data){
        list = [];
        data.headers = {
            'X-Token-Website': $ApplicationData.token_website,
        }
        return data;
    });
}

function stateDropdown(name, value, event) {
    salesPaginator.update((object)=>{
        object.conditions.status = value;
        return object; 
    })
    list_application.fetch();
    
}

function setStatus(statusString){
    const status_application = new FetchApp(salesPaginator, (data) => {
        salesPaginator.update((object) => {
            object.statusOrders[statusString] = data.items;
            return object;
        });
    })
    
    status_application.before_fetch = ()=>{
        salesPaginator.update((objectData) => {
            objectData.conditions.status = statusString;
            return objectData; 
        })
    }
    status_application.fetch();
    
    salesPaginator.update((objectData) => {
        objectData.conditions.status = "";
        return objectData; 
    })
}

function processHanddler() {
    ChecksSelectedData.update("from_path", "projects/orders");
    if ($ChecksSelectedData.checks_selected.length > 1) {
        page.route('projects/orders/multiple/process');
    }else{
        page.route(`projects/orders/${$ChecksSelectedData.checks_selected[0]}/process`);
    }
}

$: {
    if($ApplicationData.website_selected){
        list_application.fetch();
        //setStatus("pending");
        //setStatus("completed");
    }
}

</script>
{#if $ApplicationData.has_projects === true}
    <section>
        <HeaderBoxProjects paginatorData={salesPaginator} list_application_fetch={list_application} setStatus={setStatus}/>
        <div class="is-flex is-justify-content-space-between gap-14 py-4">
            <StateDropdown on_change={stateDropdown}/>
            <SearchInput paginatorData={salesPaginator} list_application_fetch={list_application}/>
        </div>
        <div class="dropdowns-zone line flex-center-space-between">
            <p class="has-text-gray fw600" style="font-size: 20px;">Ordenes (<span class="fw300" style="font-size:16px;">{$ChecksSelectedData.checks_selected.length ?? 0} seleccionadas</span>)</p>
            <div class="flex-center" style="gap: 25px;">
                <button type="button" class="button brand is-primary is-rounded" disabled={$ChecksSelectedData.checks_selected?.length === 0} on:click={()=>{processHanddler()}}>
                    {#if $ChecksSelectedData.checks_selected?.length === 0}
                        Procesar Selección
                    {:else if $ChecksSelectedData.checks_selected?.length === 1}
                        Procesar orden
                    {:else}
                        Procesar ordenes
                    {/if}
                </button>
            </div>
        </div>
        <div class="table-container" style="overflow-x: auto;">
            {#if list?.data?.length > 0}
            <ListSalesReport paginatorData={salesPaginator} fetcher={list_application} />
                {:else if  list.items === 0}
                    <NoElementsTable />
                {:else}
                    <LoaderElement style_container="min-height:400px;" />
            {/if}
        </div>
        <div class="flex-start-flex-end">
            <Paginator paginatorData={salesPaginator} list_application_fetch={list_application} />
        </div>
    </section>
    {:else}
        <LoaderElement style_container="min-height:400px;"/>
{/if}

<style>
    .dropdowns-zone{
        width: 100%;
        padding: 1rem 0
    }

    .dropdowns-zone button.brand[disabled]{
        background: #B4C0C9;
    }

    .has-text-gray{
        color: #415B76;
    }

    @media only screen and (max-width: 940px){
        .dropdowns-zone{
            flex-direction: column;
            align-items: center;
        }
    }
</style>