<script>
import DetailLayout from './DetailLayout.svelte';
import HeaderTabs from './../views/projects/HeaderTabs.svelte';
import { RouterStore } from './../RouterStore.js';
import LoaderElement from './../components/Elements/LoaderElement.svelte';
import { ApplicationData } from './../data.application.route.js';
import ProjectsDropdown from './../components/Dropdowns/ProjectsDropdown.svelte';
import { InlineRoute } from './../inline.routes.js';
import {ProjectWebsiteInstance} from "./LayoutLibraries/ProjectWebsiteInstance"
import { ChecksSelectedData } from '../data.checks_selected';
export let props = {};
let description = $RouterStore.description ?? "";
let title = $RouterStore.title ?? "";
let routes = $RouterStore.routes ?? [];
let parameters = $RouterStore.parameters ?? {};

let show = false;


if(($InlineRoute?.path ?? "") === ""){
    show = true;
}

var queryParams = new URLSearchParams(window.location.search);
let project_selected = +(queryParams.get('project_id') ?? 0);
let website_selected = +(queryParams.get('website_id') ?? 0);


let instanceProject;

if($RouterStore.props.load_website_details){
    instanceProject = new ProjectWebsiteInstance($ApplicationData);
    instanceProject.callback = function(){
        show = true;
    }
    instanceProject.fetch(project_selected, website_selected)
    
}

if($ApplicationData.project_id !== null){
    show = true;
}



ApplicationData.addMethod("website_changer", function(){
    show = false;
    instanceProject = new ProjectWebsiteInstance($ApplicationData);
    instanceProject.callback = function(){
        show = true;
    }
    instanceProject.fetch();
})



if(typeof $RouterStore.parameters?.order === "string"){
    title = `Orden: #00${$RouterStore.parameters.order}`;
    description = "";
}



</script>
<style>
.general-container{
    min-height: 100vh;
    color: #415B76;
    margin-top: 40px;
}
.general-card-container{
    border-radius:  0 0 20px 20px;
}
.data-container{
    border-radius: 20px 20px 0 0;
}
.routes-container div p{
    color: #35465B;
}
.main-section-layout{
    border-radius: 20px;
    min-height: 300px;
    height: fit-content;
}
@media only screen and (max-width: 1023px){
    .general-container{
        width: 100%;
        margin-left: 0;
    }
}
@media only screen and (max-width: 450px){
    .general-card-container{
        width: 100% !important;
        padding: 36px 2.25rem;
    }
    .general-container{
        padding: 4.25rem 0.35rem;
    }
}
.general-container:global(.home-styles .general-card-container){
    background-color: #fff !important;
    background: url('https://raw.githubusercontent.com/3pod-us/3pod-us.github.io/master/pub-img/900.jpg'), #fff !important;
    background-repeat: no-repeat !important;
    min-height: 580px !important;
    background-size: contain !important;
}
@media only screen and (max-width:1023px){
    .general-container:global(.home-styles .general-card-container){
        background-color: #fff !important;
        background: #fff !important;
    }
}
</style>
<section class="general-container flex-column-space-evenly {($InlineRoute.path === "/" || $InlineRoute.path === "") ? 'home-styles' : ''}">
    <main class="columns is-multiline is-centered">
        <div class="column is-9">
            {#if routes.length >= 1 }
                <div class="flex-center mb-alt-4" style="width: fit-content;">
                    <p class="has-text-weight-mediums">
                        {#each routes as route, key}
                            <a href="{route.path}" class="has-text-grey fw500">
                                {route.name}                                             
                            </a>
                            {#if (key+1) !== routes.length}
                                <span>> </span>
                            {/if}
                        {/each}
                    </p>
                </div>
            {/if}
            <section class="main-section-layout has-shadow">
                <div class="data-container px-alt-8 pt-alt-8 has-background-white">
                    <div class="routes-container">
                        <div style="padding: 10px 0;">
                            {#if title != ""}
                                <h1 class="fw600 is-size-3-desktop font-size-28 font-size-24-mobile" style="font-size: 36px;">{title}</h1>
                            {/if}
                            {#if description != ""}
                                <p class="py-alt-2 font-size-16">{description}</p>
                            {/if}
                        </div>
                        <div class="projects-dropdowns py-alt-4">
                            {#if ($InlineRoute?.path ?? "") !== ""}
                                <ProjectsDropdown />
                            {/if}
                        </div>
                        {#if show}
                            {#if ($InlineRoute?.path ?? "") !== ""}
                                <HeaderTabs />
                            {/if}
                        {/if}
                    </div>
                </div>
                <div class="general-card-container pb-alt-7 px-alt-8 has-background-white">
                    <section class="p-alt-8 custom-scrollbar-gray">
                        {#if show}
                                <slot>
                                    <!-- EN ESTE SLOT SE INSERTARIA EL ELEMENTO DE LA VISTA Routes que corresponda -->
                                </slot>
                            {:else}
                                <LoaderElement style_container="min-height:400px;"/>
                        {/if}
                    </section>
                </div>
            </section>
        </div>
    </main>
</section>