import { writable } from 'svelte/store';

function checksData() {

	const { subscribe, update } = writable({
        checks_selected: [],
        from_path: ""
	});
	
	const dataApp = {
        subscribe: subscribe,
        update: function(field, new_data){
            update(( data )=>{
                data[field] = new_data;
                return data;
            })
        },
    }

    return dataApp;
}


export const ChecksSelectedData = checksData();