<script>
import LoaderElement from './../../../../components/Elements/LoaderElement.svelte';
import { ApplicationData } from './../../../../data.application.route.js';
import CommonAppsLayout from '../components/CommonAppsLayout.svelte';
import HeaderBoxProjects from '../../HeaderBoxProjects.svelte';
import { CustomStore } from '../../../../libraries/customStore/CustomStore';

export let application_name = "effi";
let underLineText = "Importar productos";

/**
 * copier(): this function detects a click event on specific element and make a copi of elemnt content
 * @param e: is the event that was catched on element.
 */
function copier(e){
    let inputElement = e.target.previousElementSibling;
    inputElement.select(); 
    document.execCommand("copy"); 
}

let token_data = null;
fetch(`${$ApplicationData.api_host}/public/api/v1/services/user/effi/tokens/${$ApplicationData.user.public_key}`, {
    headers: {'X-Token-Website': $ApplicationData.token_website},
})
.then(( r )=>{
    return r.json();
}).then(( data )=>{
    token_data = data
})
let application = null;
$ApplicationData.app_store.map(( app )=>{
    if(app.brand_id === application_name){
        application = app;
    }
})

let is_generating = false;
function generate(){
    if(is_generating === true){return;}
    fetch(`${$ApplicationData.api_host}/public/api/v1/services/user/effi/tokens/${$ApplicationData.user.public_key}`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
        method: "PATCH",
    })
    .then(( r )=>{
        return r.json();
    }).then(( data )=>{
        token_data = data
        is_generating = false;
    })
}

let FormDataApplication = new CustomStore({
    form: {
        install: false,
    }
}, ()=>{ FormDataApplication = FormDataApplication; })


let switchStatus = false;
let steps = [];
$: if(token_data !== null){
    if(typeof application.content === "string"){
        application.content = JSON.parse(application.content);
    }

    steps = application.content.steps
}
</script>

{#if token_data !== null}
    <section>
        <div class="is-line flex-center-flex-start pb-alt-8">
            <a href="#path:/projects/applications" class="flex-center-flex-start">
                <i class="icon-tripod-arrow-back is-size-2"></i>
                <p style="margin-left: 1rem;">volver</p>
            </a>
        </div>
        <CommonAppsLayout FormDataApplication={FormDataApplication} application_name={application_name} switchStatus={switchStatus} application="{application_name}" type="default" steps={steps} logoUrl={application.logos[0]} underLineText={underLineText}>
            <section class="effi__container">
                <article class="effi__main">
                    <div class="effi__configuration">
                        <h2 class="is-size-5 has-text-grey has-text-weight-semibold mb-5 mx-5">Configuración</h2>
                        <div class="control mt-3">
                            <div class="field">
                                <p class="has-text-grey has-text-weight-medium mx-5">Dominio</p>
                                <div class="is-block is-relative">
                                    <input type="text" class="input effi__input mx-5 has-text-grey has-text-weight-medium" value="https://{$ApplicationData.website_selected.subdomain}.mi3pod.com" readonly placeholder="Token de acceso">                            
                                    <span class="copy-icon">
                                        <i class="icon-tripod-duplicate" on:click={(e) => copier(e)}></i>
                                    </span>
                                </div> 
                            </div>
                        </div> 
                        <div class="control mt-3">
                            <div class="field">
                                <p class="has-text-grey has-text-weight-medium mx-5">Website token</p> 
                                <div class="is-block is-relative">
                                    <input type="text" class="input effi__input mx-5 has-text-grey has-text-weight-medium" value="{$ApplicationData.token_website}" readonly>                            
                                    <span class="copy-icon">
                                        <i class="icon-tripod-duplicate" on:click={(e) => copier(e)}></i>
                                    </span>
                                </div>  
                            </div>
                        </div>   
                        <div class="control mt-3">
                            <div class="field">
                                <p class="has-text-grey has-text-weight-medium mx-5">Token de acceso</p> 
                                <div class="is-block is-relative">
                                    <input type="text" class="input effi__input mx-5 has-text-grey has-text-weight-medium" value="{token_data.token ?? "No se ha podido generar un token"}" readonly placeholder="Token de acceso">                            
                                    <span class="copy-icon">
                                        <i class="icon-tripod-duplicate " on:click={(e) => copier(e)}></i>
                                    </span>
                                    <br>
                                    <p on:click|preventDefault="{()=>{generate()}}" rel="noopener noreferrer" class="is-clickable has-text-primary mx-5 mt-2">
                                        Generar nuevo token de acceso
                                    </p>
                                </div>  
                            </div>
                        </div>                      
                    </div>
                </article>
            </section>
            <svelte:fragment slot="footer">
                {#if (application.content?.footer ?? null) !== null}
                    <br>
                    <h2 class="font-size-20">
                        {application.content?.footer?.title}
                    </h2>
                    <p class="font-size-14">
                        {@html application.content?.footer?.subtitle}
                    </p>
                {/if}
            </svelte:fragment>
        </CommonAppsLayout>
    </section>
    {:else}
        <LoaderElement style_container="min-height:400px;"  />
{/if}
<style>
.effi__container{
    display: flex;
    flex-direction: column;
}

.effi__input{
    font-size: 14px;
    padding: 20px 35px 20px 22px;
    width: 55%;
    border-radius: 8px;
}

.input:focus{
    outline: none !important;
}

.copy-icon{
    position: absolute;
    transform: translate(-55px, 12px);
    font-size: 18px;
    color: var(--color-lightGrey);
    cursor: pointer;
}
.copy-icon i {
    font-size: 18px;
}

.is-line{
    width: 100%;
}

.is-line a{
    text-decoration: none;
    color: #415B76;
}


@media only screen and (max-width: 900px){
    .effi__input{
        width: 100%;
    }

    .effi__configuration h2{
        margin: 0 0 1.5rem 1.0625rem !important;
    }
    
    .effi__configuration p {
        margin: 0 0 0 1.0625rem !important;
    }
}

</style>