import {turpial} from "./turpial"
const materialize = function(callback, target){
    target = target || document.head;
    if(typeof M !== "undefined"){
        callback();
        return;
    }
    turpial.fetch({
        target: target,
        url: ["https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0/js/materialize.js"],
        ready: ()=>{
            callback();
        },
    })
}

export { materialize }