<script>
	import { ApplicationData } from './../../../../data.application.route.js';
import SimpleModal from './../../../../components/Modals/SimpleModal.svelte';
import CommonAppsLayout from '../components/CommonAppsLayout.svelte';
import FormModal from './components/FormModal.svelte';
import FormStep2 from './components/FormStep2.svelte';
import { CustomStore } from '../../../../libraries/customStore/CustomStore.js';
let application_name = "mipaquete";
let underLineText = "Importar productos";
        
    export let step = 1;

    
    // let hide = true;
    let title = 'mipaquete.com';
    let description = 'Aquí puedes crear y gestionar todo lo relacionado con tu página web';


    let logoUrl = "https://i.ibb.co/cDGC19d/logo-mipaquete-1.png";
    let steps = [
        "Ingresa a tu cuenta de mipaquete.",
        "Ajusta tus preferencias de envio",
        `Dentro de tu funnel crea un metodo de pago llamado "Mipaquete.com" en modo contraentrega.`];

    let dataForms = {};
    let bottomStep = "";
    
    let changeStep = function(s){
        step = s;
    } 

$: {
    if(step == 2){
        bottomStep = "Si tus envíos son con pago contraentrega debes tener registrados los datos bancarios en tu perfil de usuario en la plataforma https://app.mipaquete.com/usuario/perfil-usuario"
    }
}

let FormDataApplication = new CustomStore({
    form: {
        install: false,
    }
}, ()=>{ FormDataApplication = FormDataApplication; })


let switchStatus = false;
function on_save(form, button) {
    ButtonStatus(button, "disabled");
    new ApplicationInstaller({
        ApplicationData: $ApplicationData,
        application_name: application_name,
        install: form.install,
        data: form,
        updater: ApplicationData,
        callback: function(data){
            ApplicationData.update("token_website", data.token);
            ApplicationData.update("app_store", data.builder_apps.data);
            ApplicationData.update("website_data", data.website);

            ButtonStatus(button, "enabled");
            button.setAttribute("disabled", "");
        }
    })
}

</script>

<section>
    <div class="is-line flex-center-flex-start mb-3">
        <a href="#path:/projects/applications" class="flex-center-flex-start">
            <i class="icon-tripod-arrow-back is-size-2"></i>
            <p style="margin-left: 1rem;">volver</p>
        </a>
    </div>
    <CommonAppsLayout FormDataApplication={FormDataApplication} application_name={application_name} switchStatus={switchStatus} application="{application_name}" on_save={on_save} type="default" steps={steps} logoUrl={logoUrl}>
        <!-- CONTENT -->
        <article class="effi__main columns mb-6 w-100" >
            <!-- FIRST COLUMN -->
            <div class="effi__configuration column">
                <h2 class="is-size-5 has-text-gray has-text-weight-semibold mb-6 mx-5">Configuración</h2>
                {#if step === 1}
                    <SimpleModal triggerClass="w-100" styles="max-width: 600px;" classesHeader="" classesContent="px-5 pt-1 w-100 alternative" closeOnClickOverlay="false">
                        <div slot="content">
                            <FormModal changeStep={changeStep} dataForms={dataForms}/>
                        </div>
                        <div slot="trigger">
                            <button class="effi__btn has-text-white has-text-weight-medium mx-4">
                                <i class="fas fa-question has-text-white mx-3"></i>
                                Ingresar con mipaquete.com
                            </button>
                        </div>
                    </SimpleModal>
                {/if}
                {#if step === 2}
                    <button class="effi__btn account has-text-white has-text-weight-medium mx-4">
                        <i class="fas fa-question has-text-white mx-3"></i>
                        Conectado con la cuenta: <br>
                        <div class="flex-center-space-between px-6">
                            {dataForms.user}
                            <span class="link-item">Cambiar</span>
                        </div>
                    </button>
                    <FormStep2/>
                {/if}
            </div>
        </article>
    </CommonAppsLayout>
</section>

<style>
    .w-100{
        width: 100%;
    }
    .link-item{
        text-decoration: underline;
        color: #35465B;
        cursor: pointer;
    }

    .effi__btn{
        background: #EF8503 padding-box;
        font-size: 14px;
        padding: 23px 80px;
        text-align: center;
        width: 80%;
        border-radius: 8px;
    }

    .is-line{
        width: 100%;
    }

    .is-line a{
        text-decoration: none;
        color: #415B76;
    }

    @media screen and (max-width:900px){
        .effi__btn{
            width: 100%;
        }
        .effi__btn.account{
            text-align: left;
            margin-bottom: 3rem;
        }
    }
</style>